@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
body{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100vw;height:100vh;background-color:#6d695c;background-image:repeating-linear-gradient(120deg, rgba(255,255,255,0.1), rgba(255,255,255,0.1) 1px, transparent 1px, transparent 60px),repeating-linear-gradient(60deg, rgba(255,255,255,0.1), rgba(255,255,255,0.1) 1px, transparent 1px, transparent 60px),linear-gradient(60deg, rgba(0,0,0,0.1) 25%, transparent 25%, transparent 75%, rgba(0,0,0,0.1) 75%, rgba(0,0,0,0.1)),linear-gradient(120deg, rgba(0,0,0,0.1) 25%, transparent 25%, transparent 75%, rgba(0,0,0,0.1) 75%, rgba(0,0,0,0.1));background-size:70px 120px}.container{border:30px solid #40210f;border-image:linear-gradient(90deg, #40210f 39%, #9c4300 89%, #4b4b4b 100%) 30;padding:7rem;background-image:url("https://www.textures.com/system/gallery/photos/Wood/Fine%20Wood/114255/WoodFine0071_18_download600.jpg");background-repeat:no-repeat;background-size:cover;width:50vw;height:50vh}h1,h2{text-decoration:underline}.input,.output{border:0.2em solid;border-radius:10px;font-size:1.5rem}.input{font-family:'Kaushan Script', cursive;overflow:hidden}

